import GoodLoader from "../shared/components/GoodLoader/GoodLoader";
import React from "react";


export function getFakeProducts (quantity: number = 8) {
    const LoaderGoodsArr = [...Array(quantity)]

    return LoaderGoodsArr.map( (iteam, index) => {
        return <GoodLoader key={index}/>
    })
}


export default class FakeItems {
    static getFakeProducts (quantity: number = 8) {
        const LoaderGoodsArr = [...Array(quantity)]

        return LoaderGoodsArr.map( (iteam, index) => {
            return <GoodLoader key={index}/>
        })
    }
}