import {productAction, productActions, productState} from "./types";

const initialState: productState = {
    products: [],
    isLoadingData: false,
    errorValue: null
}

export const productsReducer = (state = initialState, actions: productAction): productState => {
    switch (actions.type) {
        case productActions.SET_PRODUCTS:
            return {...state, products: actions.payload}
            break;
        case productActions.SET_IS_LOADING_PRODUCTS:
            return {...state, isLoadingData: actions.payload}
            break;
        case productActions.SET_ERROR_PRODUCTS:
            return {...state, errorValue: actions.payload}
            break;
        default:
             return state

    }
}