import {IProduct} from "../../../models/IProduct";

export interface productState {
    products: IProduct[],
    isLoadingData: boolean,
    errorValue: string | null
}

export enum productActions {
    SET_PRODUCTS = 'PRODUCTS/SET_PRODUCTS',
    SET_IS_LOADING_PRODUCTS = 'PRODUCTS/SET_IS_LOADING_PRODUCTS',
    SET_ERROR_PRODUCTS = 'PRODUCTS/SET_ERROR_PRODUCTS'
}

export interface ISetProductsAction {
    type: productActions.SET_PRODUCTS
    payload: IProduct[]
}

export interface ISetIsLoadingProductsAction {
    type: productActions.SET_IS_LOADING_PRODUCTS
    payload: boolean
}

export interface ISetErrorProductsAction {
    type: productActions.SET_ERROR_PRODUCTS
    payload: string | null
}


export type productAction = ISetProductsAction | ISetIsLoadingProductsAction | ISetErrorProductsAction