import React, {FC} from 'react';
import Button from "../../UI/Button/Button";
import CartProduct from "../Products/CartProduct/CartProduct";
import {IProduct} from "../../../models/IProduct";
import CartProductsContainer from "../ProductsContainer/CartProductsContainer";

interface EmptyCartProps {
    products: IProduct[]
}

const NotEmptyCart: FC<EmptyCartProps> = ({products}) => {

    let sum: number = products.reduce((acc: number, item: IProduct) => acc+item.price ,0)
    let nalog: number = Number((sum * 0.05).toFixed(2))

    return (
        <>
            <div className="sidemenu__iteams-conteainer">
                <CartProductsContainer products={products}/>
            </div>
            <div className="sidemenu__finalprice-container">
                <div className="sidemenu__finalprice">
                    <div className="sidemenu__txt">Итого: </div>
                    <div className="sidemenu__dashedelement"></div>
                    <span className="sidemenu__sum">{sum+nalog}</span>
                </div>
                <div className="sidemenu__tax">
                    <div className="sidemenu__txt">Налог 5%: </div>
                    <div className="sidemenu__dashedelement"></div>
                    <span className="sidemenu__sumtax">{nalog}</span>
                </div>
            </div>
            <Button text={"Оформить заказ"}/>
        </>
    );
};

export default NotEmptyCart;