import {IProduct} from "../../../models/IProduct";
import {
    ISetErrorProductsAction,
    ISetIsLoadingProductsAction,
    ISetProductsAction,
    productAction,
    productActions
} from "./types";
import {Dispatch} from "react";
import appService from "../../../API/appService";
import Dalay from "../../../utils/Dalay";
import {cartAction} from "../cart/types";


const setProductsAction = (products: IProduct[]): ISetProductsAction => {
    return {
        type: productActions.SET_PRODUCTS,
        payload: products
    }
}
const setIsLoadingAction = (isLoading: boolean): ISetIsLoadingProductsAction => {
    return {
        type: productActions.SET_IS_LOADING_PRODUCTS,
        payload: isLoading
    }
}
const setErrorAction = (error: string): ISetErrorProductsAction => {
    return {
        type: productActions.SET_ERROR_PRODUCTS,
        payload: error
    }
}


export function fetchProducts() {
    return async (dispatch: Dispatch<productAction>) => {
        try {
            dispatch(setIsLoadingAction(true))
            const response = await appService.getProducts()
            dispatch(setProductsAction(response.data))
            await Dalay.wait(2)
        } catch (e){
            dispatch(setErrorAction((e as Error).toString()))
        } finally {
            dispatch(setIsLoadingAction(false))
        }
    }
}

export function fetchFavoritesProducts(ids: string[]) {
    return async (dispatch: Dispatch<productAction>) => {
        try {
            dispatch(setIsLoadingAction(true))

            let arrOfProducts: IProduct[] = []
            for(const id of ids) {
                const response = await appService.getProductById(id)
                arrOfProducts.push(response.data)
            }
            dispatch(setProductsAction(arrOfProducts))

        } catch (e){
            dispatch(setErrorAction((e as Error).toString()))
        } finally {
            dispatch(setIsLoadingAction(false))
        }
    }
}